'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import ChatInterface from '@/components/ChatInterface';
import { mockAnomaService, MockMatch } from '@/lib/mockAnomaService';

export default function ChatPage() {
  const [matches, setMatches] = useState<MockMatch[]>([]);
  const [selectedMatch, setSelectedMatch] = useState<MockMatch | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const currentUser = mockAnomaService.getCurrentUser();

  useEffect(() => {
    if (currentUser) {
      loadMatches();
    } else {
      setIsLoading(false);
    }
  }, [currentUser]);

  const loadMatches = async () => {
    if (!currentUser) return;

    try {
      setIsLoading(true);
      const userMatches = await mockAnomaService.getMatches(currentUser.id);
      setMatches(userMatches.filter(match => match.status === 'accepted'));
    } catch (err) {
      setError('Failed to load conversations');
    } finally {
      setIsLoading(false);
    }
  };

  const handleMatchSelect = (match: MockMatch) => {
    setSelectedMatch(match);
  };

  const handleBack = () => {
    setSelectedMatch(null);
  };

  if (!currentUser) {
    return (
      <div className="min-h-screen bg-theme-black text-theme-white">
        {/* Navigation */}
        <nav className="border-b border-theme-white/10 bg-theme-black/95 backdrop-blur-sm">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
              <Link href="/" className="flex items-center">
                <h1 className="text-2xl font-bold text-gradient-red-green">
                  Anoma Skills
                </h1>
              </Link>
            </div>
          </div>
        </nav>

        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <Card className="mystical-card">
            <CardContent className="pt-6 text-center">
              <div className="text-4xl mb-4">🔒</div>
              <h3 className="text-xl font-bold text-theme-white mb-2">Wallet Not Connected</h3>
              <p className="text-gray-300 mb-4">
                Please connect your Anoma wallet to access your conversations.
              </p>
              <Link href="/onboarding">
                <Button className="btn-primary">
                  Connect Wallet
                </Button>
              </Link>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-theme-black text-theme-white">
      {/* Navigation */}
      <nav className="border-b border-theme-white/10 bg-theme-black/95 backdrop-blur-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <Link href="/" className="flex items-center">
              <h1 className="text-2xl font-bold text-gradient-red-green">
                Anoma Skills
              </h1>
            </Link>
            <div className="hidden md:block">
              <div className="ml-10 flex items-baseline space-x-4">
                <Link href="/" className="nav-link">
                  Home
                </Link>
                <Link href="/onboarding" className="nav-link">
                  Get Started
                </Link>
                <Link href="/post-intent" className="nav-link">
                  Post Intent
                </Link>
                <Link href="/matches" className="nav-link">
                  Find Matches
                </Link>
              </div>
            </div>
          </div>
        </div>
      </nav>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {selectedMatch ? (
          /* Chat Interface */
          <div className="h-[calc(100vh-8rem)] animate-slide-up">
            <ChatInterface match={selectedMatch} onBack={handleBack} />
          </div>
        ) : (
          /* Conversations List */
          <div>
            {/* Header */}
            <div className="mb-8 animate-fade-in">
              <h1 className="text-4xl md:text-5xl font-bold mb-4">
                <span className="text-gradient-red-white">Your</span>
                <span className="text-gradient-green-white"> Conversations</span>
              </h1>
              <p className="text-xl text-gray-300">
                Secure, encrypted messaging with your skill exchange partners
              </p>
            </div>

            {error && (
              <div className="mb-6">
                <Card className="mystical-card border-theme-red">
                  <CardContent className="pt-6">
                    <div className="text-theme-red">{error}</div>
                  </CardContent>
                </Card>
              </div>
            )}

            {isLoading ? (
              <div className="space-y-4">
                {[1, 2, 3].map((i) => (
                  <Card key={i} className="mystical-card animate-pulse">
                    <CardContent className="pt-6">
                      <div className="flex items-center gap-4">
                        <div className="w-12 h-12 bg-theme-black/50 rounded-full"></div>
                        <div className="flex-1">
                          <div className="h-4 bg-theme-black/50 rounded mb-2"></div>
                          <div className="h-3 bg-theme-black/50 rounded w-2/3"></div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : matches.length === 0 ? (
              <Card className="mystical-card">
                <CardContent className="pt-6 text-center">
                  <div className="text-4xl mb-4">💬</div>
                  <h3 className="text-xl font-bold text-theme-white mb-2">No Conversations Yet</h3>
                  <p className="text-gray-300 mb-6">
                    You don't have any active conversations. Find matches to start chatting with potential learning partners.
                  </p>
                  <div className="flex flex-col sm:flex-row gap-4 justify-center">
                    <Link href="/matches">
                      <Button className="btn-primary">
                        Find Matches
                      </Button>
                    </Link>
                    <Link href="/post-intent">
                      <Button className="btn-secondary">
                        Post Intent
                      </Button>
                    </Link>
                  </div>
                </CardContent>
              </Card>
            ) : (
              <div className="grid gap-4">
                {matches.map((match) => {
                  const otherUser = match.teacherUser.id === currentUser.id ? match.learnerUser : match.teacherUser;
                  const isTeaching = match.teacherUser.id === currentUser.id;

                  return (
                    <Card 
                      key={match.id} 
                      className="mystical-card hover-glow-green cursor-pointer animate-slide-up"
                      onClick={() => handleMatchSelect(match)}
                    >
                      <CardContent className="pt-6">
                        <div className="flex items-center justify-between">
                          <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-gradient-red-green rounded-full flex items-center justify-center text-white font-bold">
                              {otherUser.username?.charAt(0).toUpperCase() || 'U'}
                            </div>
                            <div className="flex-1">
                              <div className="flex items-center gap-2 mb-1">
                                <h3 className="font-semibold text-theme-white">
                                  {otherUser.username || 'Anonymous User'}
                                </h3>
                                <Badge className={`text-xs ${
                                  isTeaching 
                                    ? 'bg-theme-green/20 text-theme-green' 
                                    : 'bg-theme-red/20 text-theme-red'
                                }`}>
                                  {isTeaching ? 'Learning' : 'Teaching'} {match.skill}
                                </Badge>
                              </div>
                              <p className="text-sm text-gray-300">
                                {otherUser.location && `📍 ${otherUser.location} • `}
                                {match.compatibilityScore}% compatibility
                              </p>
                            </div>
                          </div>
                          <div className="text-right">
                            <div className="text-sm text-gray-400 mb-1">
                              {new Date(match.createdAt).toLocaleDateString()}
                            </div>
                            <Badge className="bg-theme-green/20 text-theme-green">
                              Active
                            </Badge>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  );
                })}
              </div>
            )}

            {/* Quick Actions */}
            {matches.length > 0 && (
              <div className="mt-12 animate-fade-in">
                <Card className="mystical-card">
                  <CardHeader>
                    <CardTitle className="text-theme-white">⚡ Quick Actions</CardTitle>
                    <CardDescription className="text-gray-300">
                      Manage your skill exchange activities
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="grid md:grid-cols-3 gap-4">
                      <Link href="/matches">
                        <Button className="btn-primary w-full">
                          Find More Matches
                        </Button>
                      </Link>
                      <Link href="/post-intent">
                        <Button className="btn-secondary w-full">
                          Post New Intent
                        </Button>
                      </Link>
                      <Link href="/feedback">
                        <Button className="btn-outline w-full">
                          Leave Feedback
                        </Button>
                      </Link>
                    </div>
                  </CardContent>
                </Card>
              </div>
            )}

            {/* Privacy Notice */}
            <div className="mt-8 animate-fade-in">
              <Card className="mystical-card border-theme-white/20">
                <CardContent className="pt-6">
                  <div className="flex items-start gap-3">
                    <div className="text-2xl">🔒</div>
                    <div className="text-sm text-gray-300">
                      <strong className="text-theme-white">End-to-End Encryption:</strong> All your 
                      conversations are protected with advanced encryption. Messages are only visible 
                      to you and your conversation partner. Your privacy is our priority.
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
