'use client';

import { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { mockAnomaService, MockMatch, MockMessage } from '@/lib/mockAnomaService';

interface ChatInterfaceProps {
  match: MockMatch;
  onBack?: () => void;
}

export default function ChatInterface({ match, onBack }: ChatInterfaceProps) {
  const [messages, setMessages] = useState<MockMessage[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [isEncrypted, setIsEncrypted] = useState(true);
  const [isSending, setIsSending] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const currentUser = mockAnomaService.getCurrentUser();
  const otherUser = match.teacherUser.id === currentUser?.id ? match.learnerUser : match.teacherUser;
  const isTeaching = match.teacherUser.id === currentUser?.id;

  useEffect(() => {
    loadMessages();
  }, [match.id]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const loadMessages = async () => {
    try {
      const matchMessages = await mockAnomaService.getMessages(match.id);
      setMessages(matchMessages);
    } catch (err) {
      setError('Failed to load messages');
    }
  };

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!newMessage.trim() || !currentUser) return;

    setIsSending(true);
    setError(null);

    try {
      const message = await mockAnomaService.sendMessage(
        match.id,
        newMessage.trim(),
        isEncrypted
      );
      
      setMessages(prev => [...prev, message]);
      setNewMessage('');
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to send message');
    } finally {
      setIsSending(false);
    }
  };

  const formatTime = (timestamp: string) => {
    return new Date(timestamp).toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    });
  };

  const formatDate = (timestamp: string) => {
    const date = new Date(timestamp);
    const today = new Date();
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);

    if (date.toDateString() === today.toDateString()) {
      return 'Today';
    } else if (date.toDateString() === yesterday.toDateString()) {
      return 'Yesterday';
    } else {
      return date.toLocaleDateString();
    }
  };

  if (!currentUser) {
    return (
      <Card className="mystical-card">
        <CardContent className="pt-6 text-center">
          <div className="text-4xl mb-4">🔒</div>
          <h3 className="text-xl font-bold text-theme-white mb-2">Authentication Required</h3>
          <p className="text-gray-300">Please connect your wallet to access chat.</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="flex flex-col h-full">
      {/* Chat Header */}
      <Card className="mystical-card mb-4">
        <CardHeader className="pb-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              {onBack && (
                <Button onClick={onBack} className="btn-outline p-2">
                  ←
                </Button>
              )}
              <div className="w-10 h-10 bg-gradient-red-green rounded-full flex items-center justify-center text-white font-bold">
                {otherUser.username?.charAt(0).toUpperCase() || 'U'}
              </div>
              <div>
                <CardTitle className="text-theme-white text-lg">
                  {otherUser.username || 'Anonymous User'}
                </CardTitle>
                <CardDescription className="text-gray-300">
                  {isTeaching ? 'Learning' : 'Teaching'} {match.skill}
                </CardDescription>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <Badge className={`${
                match.status === 'accepted' 
                  ? 'bg-theme-green/20 text-theme-green' 
                  : 'bg-theme-red/20 text-theme-red'
              }`}>
                {match.status}
              </Badge>
              <div className="text-sm text-gray-400">
                {match.compatibilityScore}% match
              </div>
            </div>
          </div>
        </CardHeader>
      </Card>

      {/* Messages Area */}
      <Card className="mystical-card flex-1 flex flex-col">
        <CardContent className="flex-1 flex flex-col p-0">
          {/* Messages List */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4 custom-scrollbar">
            {messages.length === 0 ? (
              <div className="text-center py-8">
                <div className="text-4xl mb-4">💬</div>
                <h3 className="text-lg font-semibold text-theme-white mb-2">Start the Conversation</h3>
                <p className="text-gray-300 mb-4">
                  Send your first message to {otherUser.username || 'your match'} and begin your skill exchange journey!
                </p>
                <div className="text-sm text-gray-400">
                  Tip: Introduce yourself and discuss your learning goals or teaching approach.
                </div>
              </div>
            ) : (
              <>
                {messages.map((message, index) => {
                  const isOwn = message.senderId === currentUser.id;
                  const showDate = index === 0 || 
                    formatDate(message.timestamp) !== formatDate(messages[index - 1].timestamp);

                  return (
                    <div key={message.id}>
                      {showDate && (
                        <div className="text-center text-xs text-gray-400 my-4">
                          {formatDate(message.timestamp)}
                        </div>
                      )}
                      <div className={`flex ${isOwn ? 'justify-end' : 'justify-start'}`}>
                        <div className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                          isOwn 
                            ? 'bg-theme-red text-theme-white' 
                            : 'bg-theme-black/50 border border-theme-white/20 text-theme-white'
                        }`}>
                          <div className="text-sm">{message.message}</div>
                          <div className="flex items-center justify-between mt-1">
                            <div className={`text-xs ${
                              isOwn ? 'text-theme-white/70' : 'text-gray-400'
                            }`}>
                              {formatTime(message.timestamp)}
                            </div>
                            {message.encrypted && (
                              <div className="text-xs text-theme-green">🔒</div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
                <div ref={messagesEndRef} />
              </>
            )}
          </div>

          {/* Error Display */}
          {error && (
            <div className="p-4 border-t border-theme-white/10">
              <Alert className="border-theme-red bg-theme-red/10">
                <AlertDescription className="text-theme-red">
                  {error}
                </AlertDescription>
              </Alert>
            </div>
          )}

          {/* Message Input */}
          <div className="p-4 border-t border-theme-white/10">
            <form onSubmit={handleSendMessage} className="space-y-3">
              {/* Encryption Toggle */}
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <Switch
                    id="encryption"
                    checked={isEncrypted}
                    onCheckedChange={setIsEncrypted}
                  />
                  <Label htmlFor="encryption" className="text-sm text-gray-300">
                    End-to-end encryption {isEncrypted ? 'enabled' : 'disabled'}
                  </Label>
                </div>
                <div className="text-xs text-gray-400">
                  {isEncrypted ? '🔒 Secure' : '🔓 Plain text'}
                </div>
              </div>

              {/* Input and Send */}
              <div className="flex gap-2">
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Type your message..."
                  className="mystical-input flex-1"
                  disabled={isSending}
                />
                <Button
                  type="submit"
                  disabled={!newMessage.trim() || isSending}
                  className="btn-primary px-6"
                >
                  {isSending ? (
                    <div className="w-4 h-4 border-2 border-theme-white border-t-transparent rounded-full animate-spin"></div>
                  ) : (
                    'Send'
                  )}
                </Button>
              </div>
            </form>
          </div>
        </CardContent>
      </Card>

      {/* Match Info */}
      <Card className="mystical-card mt-4">
        <CardContent className="pt-4">
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <div className="text-gray-400 mb-1">Skill</div>
              <div className="text-theme-white font-medium">{match.skill}</div>
            </div>
            <div>
              <div className="text-gray-400 mb-1">Match Score</div>
              <div className="text-theme-green font-bold">{match.compatibilityScore}%</div>
            </div>
            <div className="col-span-2">
              <div className="text-gray-400 mb-2">Why this is a good match:</div>
              <div className="flex flex-wrap gap-1">
                {match.matchReasons.map((reason, index) => (
                  <Badge key={index} className="bg-theme-white/10 text-theme-white text-xs">
                    {reason}
                  </Badge>
                ))}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
